import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Row, Col } from 'antd';
import { InfoCard } from 'components/common';
import {
  getPaperTypeNo,
  getNationNo,
  getTownNo,
  getDistrictNo,
  getEthinicityNo,
  getEnrollStudyFieldNo,
  GetSchoolNo,
  getQuickLinkComeFromNo,
  getQuickLinkEnrollmentGetAreaNo,
} from 'services/quicklink';
import ComeFromIcon from 'components/icons/ComeFromIcon';

const COLORS = [
  '#0088FE',
  '#00C49F',
  '#FFBB28',
  '#FF8042',
  '#0088fed9',
  '#00c49fd9',
  '#ffbb28d9',
  '#ff8042d9',
];

const data = [
  {
    title: 'Loại giấy tờ',
    key: 'SystemTraining',
    url: '/PaperType/list',
    service: getPaperTypeNo,
  },
  {
    title: 'Quốc tịch',
    key: 'SystemTraining',
    url: '/nationalitys/list',
    service: getNationNo,
  },
  {
    title: 'Tỉnh',
    key: 'SystemTraining',
    url: '/town/list',
    service: getTownNo,
  },
  {
    title: 'Huyện',
    key: 'SystemTraining',
    url: '/district/list',
    service: getDistrictNo,
  },
  {
    title: 'Dân tộc',
    key: 'SystemTraining',
    url: '/ethnicity/list',
    service: getEthinicityNo,
  },
  {
    title: 'Mã ngành tuyển sinh',
    key: 'SystemTraining',
    url: '/enroll-study-field/list',
    service: getEnrollStudyFieldNo,
  },
  {
    title: 'Mã trường tuyển sinh',
    key: 'SystemTraining',
    url: '/recruiting-school/list',
    service: GetSchoolNo,
  },
  {
    title: 'Xuất thân',
    service: getQuickLinkComeFromNo,
    url: '/comeFrom/list',
    icon: ComeFromIcon,
  },
  {
    title: 'Khu vực tuyển sinh',
    service: getQuickLinkEnrollmentGetAreaNo,
    url: '/zones/list',
  },
];

class DashboardEnrollment extends Component {
  state = {};
  render() {
    return (
      <Row gutter={24}>
        {data.map((item, index) => (
          <Col span={6} key={item.title}>
            <InfoCard
              backgroundColor={COLORS[index % 8]}
              title={item.title}
              service={item.service}
              icon={item.icon}
              url={item.url}
            />
          </Col>
        ))}
      </Row>
    );
  }
}

export default props => (
  <Page {...props}>
    <DashboardEnrollment {...props} />
  </Page>
);



